import { GameContext } from "../../context/GameContext"
import { useContext, useEffect, useState } from "react"
import styled from "styled-components"
import Gallery from "react-grid-gallery"
import CheckButton from "react-grid-gallery/lib/CheckButton"
import ScoreBoard from "./Scoreboard"
import { Centered, GameButton, Sidecar, RangeSlider } from "./Util"
import {
  gameData,
  isDefined,
  cleanObject,
  GAME_IMAGE_COUNT,
} from "../../util/data"
import "./Game.css"

const SIZES = {
  thumbnail: {
    height: 32,
    width: 32,
  },
  grid: {
    width: 710,
  },
}

const generateGameData = async (gameMode) => {
  const gameDataModels = {
    easy: { path: "./data/game-data.json" },
    hard: { path: "./data/game-data-20q-hard.json" },
  }
  const gameDataModel = gameDataModels[gameMode || "easy"]
  const _gameData = await gameData(gameDataModel)
  const images = _gameData.gameOptions.reduce(
    (all, x, i) => ({ ...all, [i]: x }),
    {}
  )
  return {
    gameData: _gameData,
    images,
    correctAnswers: Object.keys(images)
      .filter((i) => images[i].correct)
      .map((i) => parseInt(i)),
  }
}

const labelStyle = {
  padding: "5px",
  fontSize: "75%",
  color: "white",
  background: "rgba(0,0,0,0.5)",
}

const toImage = (image, index, tags = () => []) => {
  const img = {
    src: `img/${image.path}`,
    thumbnail: `img/${image.path}`,
    thumbnailWidth: SIZES.thumbnail.width,
    thumbnailHeight: SIZES.thumbnail.height,
    index,
    correct: image.correct,
    customOverlay: <SelectionsOverlay index={index} />,
  }
  img.tags = tags(img) || []
  return cleanObject(img)
}
const toDeerImage = (img, index) =>
  toImage(img, index, (_img) => [
    {
      value: `${img.label}`,
      title: `COMPY Says: ${img.label}`,
    },
  ])

const findYouPlayer = (gameSettings) =>
  gameSettings && gameSettings.players && gameSettings.players.length
    ? gameSettings.players[0]
    : null

const findCarnacPlayer = (gameSettings) =>
  gameSettings && gameSettings.players && gameSettings.players.length
    ? gameSettings.players[1]
    : null

const StyledSelectionsOverlay = styled.div`
  color: red;
  font-weight: bold;
  text-align: right;
  position: absolute;
  top: 0;
  right: 0;
  background-color: ${({ correct }) =>
    correct ? "rgba(75, 75, 75, 0.8)" : "rgba(75, 75, 75, 0.0)"};
  width: 100%;
  height: 100%;
`

const StyledCheckButton = styled.div`
  position: absolute;
  top: 0;
  right: 0%;
`

const SelectionsOverlay = ({ index }) => {
  const [gameSettings] = useContext(GameContext)
  const _carnac = findCarnacPlayer(gameSettings)
  const _you = findYouPlayer(gameSettings)
  return (
    <StyledSelectionsOverlay
      correct={isDefined(_you.score) && !gameSettings.images[index].correct}
    >
      {_carnac.score && _carnac.selections.includes(parseInt(index)) ? (
        <StyledCheckButton>
          <CheckButton color={_carnac.color} isSelected={true} />
        </StyledCheckButton>
      ) : null}
    </StyledSelectionsOverlay>
  )
}

const Choices = ({ settings: { noise }, gameMode }) => {
  const [gameSettings, setGameSettings] = useContext(GameContext)

  useEffect(() => {
    resetGame(gameMode, gameSettings, setGameSettings) // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [])

  useEffect(() => {
    if (gameSettings.originalImages) {
      const noiseKey = `noise${noise}`
      const hasVariant = (image, variant) =>
        "variants" in image && variant in image.variants
      setGameSettings({
        ...gameSettings,
        images: gameSettings.originalImages
          .map((image) =>
            hasVariant(image, noiseKey) ? image.variants[noiseKey] : image
          )
          .map((image, i) => ({
            ...gameSettings.images[i],
            ...toDeerImage(image, i),
          })),
      })
    } // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [noise])

  const onSelectImage = (index) => {
    const updated = { ...gameSettings }
    const you = findYouPlayer(updated)
    if (you.score) return
    var img = updated.images[index]
    if (img.hasOwnProperty("isSelected")) img.isSelected = !img.isSelected
    else img.isSelected = true
    you.selections = updated.images
      .map((x, index) => ({ ...x, index }))
      .filter((x) => x.isSelected)
      .map((x) => x.index)
    setGameSettings(updated)
  }

  const you = findYouPlayer(gameSettings)
  return (
    <div>
      <Gallery
        images={gameSettings.images}
        enableImageSelection={!you.score}
        onSelectImage={onSelectImage}
        onClickThumbnail={onSelectImage}
        tagStyle={labelStyle}
        showLightboxThumbnails={true}
        enableLightbox={false}
      />
    </div>
  )
}

const resetGame = async (mode, original, updateFn) => {
  const updated = { ...original }
  const currentGameData = await generateGameData(mode)
  updated.currentGameData = currentGameData
  updated.originalImages = Object.keys(currentGameData.images).map(
    (k) => currentGameData.images[k]
  )
  updated.images = updated.originalImages.map(toDeerImage)
  const _carnac = findCarnacPlayer(updated)
  const _you = findYouPlayer(updated)
  _carnac.score = null
  _you.score = null

  const carnacAnswers = () => {
    return Object.keys(currentGameData.images)
      .filter((i) => {
        const img = currentGameData.images[i]
        return (
          ("carnac" in img && img.carnac) ||
          (!("carnac" in img) && img.label === "deer")
        )
      })
      .map((i) => parseInt(i))
  }
  _carnac.selections = carnacAnswers()
  _you.selections = []
  console.log(123, updated.images)
  updateFn(updated)
}

const calculateScore = (gameData, selections) => {
  const _selections = selections || []
  const wrongAnswers = _selections.filter(
    (x) => !gameData.correctAnswers.includes(x)
  )
  const omittedAnswers = gameData.correctAnswers.filter(
    (x) => !_selections.includes(x)
  )

  return (
    Object.keys(gameData.images).length -
    wrongAnswers.length -
    omittedAnswers.length
  )
}

const StyledChallengeCarnac = styled.div`
  padding: 10px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
`

const ChallengeCarnac = ({
  onClick,
  gameMode,
  onSettingsUpdated = () => null,
}) => {
  const [gameSettings] = useContext(GameContext)
  const selectionCount = gameSettings.players[0].selections.length
  const [noiseLevel, setNoiseLevel] = useState(0)
  const changeNoiseLevel = (level) => {
    setNoiseLevel(level)
    onSettingsUpdated({ noise: level })
  }
  return (
    <StyledChallengeCarnac>
      <div>
        <p>
          COMPY has seen a lot of deer images before, and he is really good at
          identifying a deer when he sees one.
        </p>
        <p>
          Can you tell which of the following deer images COMPY may have seen?
          There is at least one.
        </p>
        <p>Select them and then hit the "Submit Answers" button (below).</p>
        <p>
          If you were to guess randomly, you would expect to get half correct.
        </p>
        <p style={{ textAlign: "center", fontWeight: "bold" }}>
          {selectionCount > 0
            ? `${selectionCount} picture(s) selected`
            : "1 or more selections required to play"}
        </p>
        {gameMode === "hard" ? (
          <RangeSlider
            label="Add Noise"
            labelPosition="bottom"
            min={0}
            max={6}
            step={1}
            value={noiseLevel}
            onChange={changeNoiseLevel}
          />
        ) : null}
      </div>
      <div style={{ display: "flex", flexDirection: "column" }}>
        <GameButton onClick={onClick} disabled={selectionCount === 0}>
          Submit Answers!
        </GameButton>
      </div>
    </StyledChallengeCarnac>
  )
}

const StyledInformation = styled(Sidecar)`
  display: flex;
  margin-top: 2px;
  height: 99%;
  border: 1px solid darkgray;
  border-top-right-radius: 15px;
  border-bottom-right-radius: 15px;
`

const Information = (props) => {
  const [gameSettings, setGameSettings] = useContext(GameContext)
  const carnac = findCarnacPlayer(gameSettings)

  const loadScores = () => {
    const updated = { ...gameSettings }
    const _carnac = findCarnacPlayer(updated)
    const _you = findYouPlayer(updated)
    _carnac.score = calculateScore(
      gameSettings.currentGameData,
      _carnac.selections
    )
    _you.score = calculateScore(gameSettings.currentGameData, _you.selections)
    setGameSettings(updated)
  }
  return (
    <StyledInformation>
      {carnac.score ? (
        <Results {...props} />
      ) : (
        <ChallengeCarnac onClick={loadScores} {...props} />
      )}
    </StyledInformation>
  )
}

const StyledResults = styled.div`
  width: 100%;
  margin: 10px;
  align-items: center;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
`

const Results = ({ gameMode }) => {
  const [gameSettings, setGameSettings] = useContext(GameContext)
  const carnac = findCarnacPlayer(gameSettings)
  const you = findYouPlayer(gameSettings)
  const playAgain = () => resetGame(gameMode, gameSettings, setGameSettings)
  const winner = () =>
    you.score > carnac.score ? you : you.score < carnac.score ? carnac : null
  const winnerMessage = () => {
    const _winner = winner()
    return _winner
      ? `The winner is ${_winner.name}!`
      : `${you.name} tied ${carnac.name}!`
  }
  return (
    <StyledResults>
      <div style={{ marginTop: 15 }}>
        <ScoreBoard
          home={you}
          away={carnac}
          bottomDisplay={winnerMessage}
          totalPossible={GAME_IMAGE_COUNT}
        />
      </div>
      <div style={{ display: "flex", flexDirection: "column", width: "100%" }}>
        <GameButton onClick={playAgain}>Play Again!</GameButton>
      </div>
    </StyledResults>
  )
}

const StyledGame = styled.div`
  display: flex;
  flex-direction: row;
`

const TwentyQuestions = ({ gameMode }) => {
  const [settings, setSettings] = useState(0)
  const settingsUpdated = (_settings) => setSettings(_settings)

  return (
    <Centered>
      <StyledGame>
        <div style={{ minWidth: SIZES.grid.width, maxWidth: SIZES.grid.width }}>
          <Choices settings={settings} gameMode={gameMode} />
        </div>
        <div style={{ maxWidth: 440, minWidth: 440 }}>
          <Information
            gameMode={gameMode}
            onSettingsUpdated={settingsUpdated}
          />
        </div>
      </StyledGame>
    </Centered>
  )
}

export default TwentyQuestions
